body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0B0C10;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.action-text-container {
  width: 80%;
  margin: 0 auto 0 auto;
}
.action-text {
  color: #66FCF1;
  font-size: 4em;
  -webkit-transition: font-size 0.2s; /* For Safari 3.1 to 6.0 */
  transition: font-size 0.2s;
  
  /* font-family: 'Pacifico', cursive; */
  font-family: 'Lobster', cursive;
  
}
.action-text:hover {
  font-size: 4.3em;
  /* width: 100%; */
}

@media only screen and (max-width: 768px) {
  .action-text {
    font-size: 30px;
  }

  .action-text:hover {
    font-size: 33px;
  }
}

.game-button-span {
  font-family: 'Lobster', cursive;
  font-size: 2em;
  color: #C5C6C7;
  -webkit-transition: font-size 0.2s; /* For Safari 3.1 to 6.0 */
  transition: font-size 0.2s;
}

.game-button-span:hover {
  font-size: 2.3em;
}

.off {
  opacity: 0.4;
  filter: alpha(opacity=50); /* For IE8 and earlier */
}


@media only screen and (max-width: 768px) {
  .game-button-span {
    font-size: 18px;
  }

  .game-button-span:hover {
    font-size: 20px;
  }
}

/* Color pallette from this beautiful configuration */
/* https://blog.visme.co/wp-content/uploads/2016/09/website12-1024x512.jpg */

.App {
  text-align: center; 
  margin-top: 5em;
}

.event-span {
  font-family: 'Lobster', cursive;
  font-size: 25px;
  color: #45A29E;
  -webkit-animation: pulse 1s infinite;
  animation: pulse 1s infinite;
}

@-webkit-keyframes pulse {
  0% { font-size: 25px }
  50% { font-size: 28px }
  100% { font-size: 25px }
}

@keyframes pulse {
  0% { font-size: 25px }
  50% { font-size: 28px }
  100% { font-size: 25px }
}

.button-container {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 5em;
  margin-bottom: 4em;
}
.comment-text {
  color: #C5C6C7;
  font-family: 'Lobster', cursive;
  font-size: 25px;
  right: 15%;
  bottom: -50px;
  position: absolute;
}

.comment-text.animate {
  -webkit-animation: up-down 2s;
          animation: up-down 2s;
}

@-webkit-keyframes up-down {
  0% { bottom: -50px; }
  10% { bottom: 20px; }
  90% { bottom: 20px; }
  100% { bottom: -50px; }
}

@keyframes up-down {
  0% { bottom: -50px; }
  10% { bottom: 20px; }
  90% { bottom: 20px; }
  100% { bottom: -50px; }
}

.comment-text.up {
  bottom: 30px
}

@media only screen and (max-width: 768px) {
  .comment-text {
    font-size: 19px;
  }
}
